import { makeAutoObservable, reaction } from "mobx";
import { ServerError } from "../models/serverError";

export default class CommonStore {
  error: ServerError | null = null;
  token: string | null = window.localStorage.getItem("jwt");
  appLoaded: boolean = false;

  constructor() {
    makeAutoObservable(this);

    reaction(
      () => {
        console.log('token yoxlanlr')  
        return this.token},
      (token) => {
        if (token) {
          //user login olanda token save olunur, v burda biz login olandaki tokeni
          // gtrb, localstorage- set eliyiriy. ks halda localstorage -d login olanda
          // token olmayacaq, nki userStore-da elbir metod tyin elmmiik ki
          // tokeni gtrb localda saxlasn. Token i app memory  d net core yox,
          // biz set eliyiriy.
          window.localStorage.setItem("jwt", token);
          console.log('[tokend dyiiklik getdi], token var',token);
        } else {
          window.localStorage.removeItem("jwt");
          console.log('[tokend dyiiklik getdi], token yoxdu',token);

        }
      }
    );
  }

  setServerError = (error: ServerError) => (this.error = error);

  setToken = (token: string) => {
    this.token = token;
  };

  setAppLoaded = () => {
    this.appLoaded = true;
  };
}
