import { ErrorMessage, Form, Formik } from "formik";
import { observer } from "mobx-react-lite";
import React from "react";
import { Button, Label,Header } from "semantic-ui-react";
import CustomTextInput from "../../app/common/form/CustomTextInput";
import { useStore } from "../../app/stores/store";

function LoginForm() {

  const {userStore} = useStore();

  return <Formik 
  initialValues={{email:'',password:'',customError:''}}
  onSubmit={(values, {setErrors})=>userStore.login(values)
  .catch(error=>setErrors({customError:'Invalid email or password'}))}
  >
      {({handleSubmit,isSubmitting,errors}) =>(
          <Form className='ui form' onSubmit={handleSubmit} autoComplete='off'>
            <Header content='Login To Reactivities' as='h2'color='teal' textAlign='center' />
             <CustomTextInput  name='email' placeholder='Email'/>    
             <CustomTextInput  name='password' placeholder='Password' type='password'/>    
             <ErrorMessage name='error' render={()=><Label style={{marginBotton:10}} basic color='red' content={errors.customError} />} />
             <Button loading={isSubmitting} positive content='Login' type='submit' fluid/>
          </Form>
      )}
  </Formik>;
}

export default observer(LoginForm);